.responsive-16-9 {
    position: relative;
    height: 0
}

.responsive-16-9 > * {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0
}

body {
    font-family: 'Open Sans', sans-serif;
    background-color: #f3f3f3;
    color: #3b3b3b;
    font-size: 1.2em;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    overflow-wrap: break-word;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Baloo', cursive;
    font-weight: unset
}

a {
    cursor: pointer;
    text-decoration: none
}

.section-container {
    max-width: 1000px;
    margin: 0 auto
}

.container-padding {
    padding: 1em
}

.footer-text {
    text-align: center;
    margin: 0.5em
}

.font-emph {
    font-family: 'Baloo', cursive
}

.responsive-16-9 {
    width: 100%;
    padding-bottom: 56.25%
}

.double-column {
    display: flex;
    padding: 1em;
    text-align: center
}

@media (max-width: 600px) {
    .double-column {
        flex-direction: column
    }
}

.double-column > div {
    flex-grow: 1
}

@keyframes fade-in {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

@keyframes zoom-out {
    from {
        transform: scale(1.5)
    }
    to {
        transform: scale(1)
    }
}

@keyframes to-right {
    from {
        transform: translateX(2em)
    }
    to {
        transform: translateX(0)
    }
}
