.section {
    text-align: center;
    margin-top: 3em;
    margin-bottom: 6em;
}


.text {
    margin-top: 4em;
    font-size: 0.8em;
}

.text > * {
    margin-bottom: 1em;
}

.image {
    box-shadow: 0px 4px 6px 4px rgba(0, 0, 0, 0.2);
}

.capsule {
    font-family: 'Baloo', cursive;
    font-size: 1.4em;
    border-radius: 1.5em;
    margin: 3em auto 3em;
    text-align: center;
    background-color: #ff8a00;
    color: white;
    padding: 0.5em 2.0em;
    display: block;
    box-shadow: 0px 4px 4px 1px rgba(0, 0, 0, 0.5)
}

.capsule:hover {
    background-color: #ff9b22;
    color: white;
    transform: scale(1.05);
    transition: transform 0.2s
}

@media (max-width: 600px) {
    .capsule {
        font-size: 1em;

    }
}