.section {
    text-align: center;
    margin-top: 3em;
    margin-bottom: 6em;
}

.columnContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 2em;
    flex-wrap: wrap;
}

.title {
    margin-bottom: 1em;
}

.column {
    display: flex;
    flex-direction: column;
    gap: 0.7em;
}

.element {
    background-color: #ff8a00;
    color: white;
    font-size: 1.3em;
    padding: 0.5em 0.9em;

    border-radius: 0.5em;
    box-shadow: 0px 2px 2px 1px rgba(0, 0, 0, 0.5)
}