.section {
    display: flex;
    flex-wrap: wrap;
    padding :1em;

}

.section > * {
    margin-top: 0.5em;
    margin-left: 0.5em
}

.headerLogo {
    width: 119px;
    min-width: 119px;
    animation: fade-in 1s ease, zoom-out 1s ease;
    transition: transform 0.2s
}

.headerLogo:hover {
    transform: scale(1.1)
}

.headerTitle {
    animation: fade-in 1s ease, to-right 1s ease
}

.title {
    font-family: 'Baloo', cursive;
    font-size: 2.5em;
    line-height: 0.9em;
    margin-top: 0.6em;
    margin-bottom: -0.1em
}

@media (max-width: 650px) {
    .section {
        flex-direction: column;
        text-align: center;
        align-items: center
    }

    .title {
        margin-top: 0
    }
}

@keyframes fade-in {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

@keyframes to-right {
    from {
        transform: translateX(2em)
    }
    to {
        transform: translateX(0)
    }
}