.section {
    text-align: center;
}


.capsule {
    font-family: 'Baloo', cursive;
    display: inline-block;
    font-size: 2em;
    border-radius: 1.5em;
    text-align: center;
    background-color: #4AA72F;
    color: white;
    padding: 0.5em 2.0em;
    box-shadow: 0px 8px 8px 1px rgba(0, 0, 0, 0.5)
}


@media (max-width: 600px) {
    .capsule {
        font-size: 1em;

    }
}