
.section {
    display: flex;
    flex-direction: column;
    padding: 1em;
    position: fixed;
    top: 30%;
    right: 0;
    align-items: center;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    margin-top: -0.5em;
    margin-left: -0.5em
}

.section > * {
    margin-top: 0.5em;
    margin-left: 0.5em
}

@media (max-width: 800px) {
    .section {
        flex-direction: row;
        position: unset;
        top: unset;
        right: unset;
        justify-content: center
    }
}

.iconWhatsapp {
    background-color: #25D366;
}

.iconWhatsapp:hover {
    background-color: #93ffbb;
}

.iconFacebook {
    background-color: #3b5998
}

.iconFacebook:hover {
    background-color: #6395ff
}

.iconLinkedin {
    background-color: #0077b5
}

.iconLinkedin:hover {
    background-color: #7fbbd9
}

.iconInstagram {
    background-color: #e1306c
}

.iconInstagram:hover {
    background-color: #f097b5
}

.circleLink {
    display: flex;
    align-items: center;
    color: white;
    box-sizing: border-box;
    justify-content: center;
    font-size: 2em;
    width: 1.7em;
    height: 1.7em;
    min-height: 1.7em;
    border-radius: 50%;
    border: 3px solid white;
    transition: transform 0.2s;
    box-shadow: 0px 2px 2px 1px rgba(0, 0, 0, 0.5)
}

.circleLink:hover {
    transform: scale(1.1)
}